import React, { useEffect, useState } from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import Navbar from './Navbar'; // Import the Navbar component
import './HomeScreen.css'; // Import the CSS file

Chart.register(...registerables);

const HomeScreen = ({ readings }) => {
  const [soilMoistureData, setSoilMoistureData] = useState({
    labels: ['No Data'],
    datasets: [{
      data: [1],
      backgroundColor: ['#d3d3d3'],
    }],
  });

  const [waterLevelData, setWaterLevelData] = useState({
    labels: ['No Data'],
    datasets: [{
      data: [1],
      backgroundColor: ['#d3d3d3'],
    }],
  });

  const [newSoilReading, setNewSoilReading] = useState('');
  const [newWaterReading, setNewWaterReading] = useState('');
  const [allReadings, setAllReadings] = useState(readings || []);
  const [motorStatus, setMotorStatus] = useState('OFF');

  useEffect(() => {
    if (Array.isArray(allReadings) && allReadings.length > 0) {
      const soilMoistureReadings = allReadings.filter(r => r.type === 'soilMoisture');
      const waterLevelReadings = allReadings.filter(r => r.type === 'waterLevel');

      if (soilMoistureReadings.length > 0) {
        setSoilMoistureData({
          labels: soilMoistureReadings.map(r => r.timestamp),
          datasets: [{
            data: soilMoistureReadings.map(r => r.value),
            backgroundColor: ['#4caf50'],
          }],
        });
      }

      if (waterLevelReadings.length > 0) {
        setWaterLevelData({
          labels: waterLevelReadings.map(r => r.timestamp),
          datasets: [{
            data: waterLevelReadings.map(r => r.value),
            backgroundColor: ['#2196f3'],
          }],
        });
      }
    }
  }, [allReadings]);

  useEffect(() => {
    // Fetch initial motor status from the IoT device
    fetchMotorStatus();
  }, []);

  const fetchMotorStatus = async () => {
    try {
      const response = await fetch('/api/motor/status'); // Replace with your actual API endpoint
      const data = await response.json();
      setMotorStatus(data.status);
    } catch (error) {
      console.error('Error fetching motor status:', error);
    }
  };

  const handleMotorButtonClick = async () => {
    const newStatus = motorStatus === 'OFF' ? 'ON' : 'OFF';
    try {
      const response = await fetch('/api/motor/control', { // Replace with your actual API endpoint
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: newStatus }),
      });
      const data = await response.json();
      if (data.success) {
        setMotorStatus(newStatus);
        alert(`Motor turned ${newStatus}`);
      } else {
        alert('Failed to control motor');
      }
    } catch (error) {
      console.error('Error controlling motor:', error);
      alert('Error controlling motor');
    }
  };

  const handleAddSoilReading = () => {
    const newReadingObj = {
      timestamp: new Date().toLocaleString(),
      value: newSoilReading,
      type: 'soilMoisture',
    };
    setAllReadings([...allReadings, newReadingObj]);
    setNewSoilReading('');
  };

  const handleAddWaterReading = () => {
    const newReadingObj = {
      timestamp: new Date().toLocaleString(),
      value: newWaterReading,
      type: 'waterLevel',
    };
    setAllReadings([...allReadings, newReadingObj]);
    setNewWaterReading('');
  };

  return (
    <div className="container">
      <Navbar /> {/* Include the Navbar component */}
      <div className="mainContent">
        <div className="chartContainer">
          <h2 className="heading">Soil Moisture Indicator</h2>
          <Pie data={soilMoistureData} />
        </div>
        <div className="chartContainer">
          <h2 className="heading">Water Level Indicator</h2>
          <Pie data={waterLevelData} />
        </div>
        <div className="readingContainer">
          <h2 className="heading">IoT Device Readings</h2>
          <input
            className="input"
            value={newSoilReading}
            onChange={(e) => setNewSoilReading(e.target.value)}
            placeholder="Enter new soil moisture reading"
          />
          <button className="button" onClick={handleAddSoilReading}>Add Soil Reading</button>
          <input
            className="input"
            value={newWaterReading}
            onChange={(e) => setNewWaterReading(e.target.value)}
            placeholder="Enter new water level reading"
          />
          <button className="button" onClick={handleAddWaterReading}>Add Water Reading</button>
          <button className="button motorButton" onClick={handleMotorButtonClick}>Control Motor</button>
          <p>Motor Status: {motorStatus}</p>
        </div>
      </div>
    </div>
  );
};

export default HomeScreen;