import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navbar from './Navbar';
import HomeScreen from './HomeScreen';
import ScheduleScreen from './ScheduleScreen';
import NotificationScreen from './NotificationScreen';
import SettingScreen from './SettingScreen';
import WeatherScreen from './WeatherScreen';
import GraphAndAnalyticsScreen from './GraphAndAnalyticsScreen';
import IoTControlScreen from './IoTControlScreen';
import IoTReadingsScreen from './IoTReadingsScreen';

function App() {
  return (
    <Router>
      <Navbar />
      <Routes>
        <Route path="/" element={<HomeScreen />} />
        <Route path="/schedule" element={<ScheduleScreen />} />
        <Route path="/notifications" element={<NotificationScreen />} />
        <Route path="/settings" element={<SettingScreen />} />
        <Route path="/weather" element={<WeatherScreen />} />
        <Route path="/analytics" element={<GraphAndAnalyticsScreen />} />
        <Route path="/iot-control" element={<IoTControlScreen />} />
        <Route path="/iot-readings" element={<IoTReadingsScreen />} />
      </Routes>
    </Router>
  );
}

export default App;