import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const ScheduleScreenContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  width: 100%;
`;

const Input = styled.input`
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 80%;
`;

const Button = styled.button`
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  font-size: 1em;
  width: 80%;

  &:hover {
    background-color: #0056b3;
  }
`;

const ScheduleList = styled.ul`
  list-style-type: none;
  padding: 0;
  width: 100%;
`;

const ScheduleItem = styled.li`
  background-color: white;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
`;

function ScheduleScreen() {
  const [schedules, setSchedules] = useState([]);
  const [start, setStart] = useState('');
  const [stop, setStop] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch existing schedules from an API or database
        const response = await fetch('/api/schedules');
        const data = await response.json();
        setSchedules(data);
      } catch (error) {
        console.error('Error fetching schedules:', error);
      }
    };

    fetchData();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const newSchedule = { start, stop };

    try {
      // Save the new schedule to an API or database
      const response = await fetch('/api/schedules', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newSchedule),
      });

      if (response.ok) {
        const savedSchedule = await response.json();
        setSchedules([...schedules, savedSchedule]);
        setStart('');
        setStop('');

        // Send command to IoT device
        await sendCommandToIoTDevice(savedSchedule);
      } else {
        console.error('Error saving schedule');
      }
    } catch (error) {
      console.error('Error saving schedule:', error);
    }
  };

  const sendCommandToIoTDevice = async (schedule) => {
    try {
      // Replace with actual API endpoint and payload for your IoT device
      const response = await fetch('/api/iot-device', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          command: 'setSchedule',
          schedule: schedule,
        }),
      });

      if (!response.ok) {
        console.error('Error sending command to IoT device');
      }
    } catch (error) {
      console.error('Error sending command to IoT device:', error);
    }
  };

  return (
    <ScheduleScreenContainer>
      <h2>Create Irrigation Schedule</h2>
      <Form onSubmit={handleSubmit}>
        <Input
          type="time"
          placeholder="Start Time"
          value={start}
          onChange={(e) => setStart(e.target.value)}
          required
        />
        <Input
          type="time"
          placeholder="Stop Time"
          value={stop}
          onChange={(e) => setStop(e.target.value)}
          required
        />
        <Button type="submit">Add Schedule</Button>
      </Form>
      <h2>Existing Schedules</h2>
      <ScheduleList>
        {schedules.map((schedule, index) => (
          <ScheduleItem key={index}>
            <span>Start: {schedule.start}</span>
            <span>Stop: {schedule.stop}</span>
          </ScheduleItem>
        ))}
      </ScheduleList>
    </ScheduleScreenContainer>
  );
}

export default ScheduleScreen;