import React, { useState, useEffect } from 'react';

function NotificationScreen() {
  const [notifications, setNotifications] = useState([]);
  const [iotData, setIotData] = useState(null);
  const [notificationsEnabled, setNotificationsEnabled] = useState(true);

  useEffect(() => {
    // Fetch notifications from API
    const fetchData = async () => {
      try {
        const response = await fetch('/api/notifications');
        const data = await response.json();
        setNotifications(data);
      } catch (error) {
        console.error('Error fetching notifications:', error);
      }
    };

    // Fetch IoT data from API
    const fetchIotData = async () => {
      try {
        const response = await fetch('/api/iot-data');
        const data = await response.json();
        setIotData(data);
      } catch (error) {
        console.error('Error fetching IoT data:', error);
      }
    };

    fetchData();
    fetchIotData();
  }, []);

  useEffect(() => {
    if (iotData && notificationsEnabled) {
      const newNotifications = [];

      if (iotData.soilMoisture < 30) {
        newNotifications.push('Low soil moisture detected.');
      }

      if (iotData.rainStatus === 'rain') {
        newNotifications.push('Rain detected.');
      }

      if (iotData.waterLevel < 20) {
        newNotifications.push('Low water level in rainwater tank.');
      }

      if (newNotifications.length > 0) {
        setNotifications((prevNotifications) => [...prevNotifications, ...newNotifications]);
      }
    }
  }, [iotData, notificationsEnabled]);

  const handleToggleNotifications = () => {
    setNotificationsEnabled(!notificationsEnabled);
  };

  return (
    <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif' }}>
      <h1>Notifications</h1>
      <button
        onClick={handleToggleNotifications}
        style={{
          padding: '10px 20px',
          fontSize: '16px',
          backgroundColor: notificationsEnabled ? '#ff4d4d' : '#4CAF50',
          color: 'white',
          border: 'none',
          borderRadius: '5px',
          cursor: 'pointer',
          marginBottom: '20px'
        }}
      >
        {notificationsEnabled ? 'Disable Notifications' : 'Enable Notifications'}
      </button>
      <p style={{ fontStyle: 'italic', color: '#555' }}>
        Notifications will be provided for low soil moisture, rain detection, and low water level in the rainwater tank.
      </p>
      <ul style={{ listStyleType: 'none', padding: 0 }}>
        {notifications.map((notification, index) => (
          <li key={index} style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>
            {notification}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default NotificationScreen;