import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const IoTReadingsContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
`;

const ReadingItem = styled.div`
  background-color: white;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 80%;
`;

function IoTReadingsScreen() {
  const [readings, setReadings] = useState([]);

  useEffect(() => {
    const fetchReadings = async () => {
      try {
        const response = await fetch('/api/iot-readings');
        const data = await response.json();
        setReadings(data);
      } catch (error) {
        console.error('Error fetching readings:', error);
      }
    };

    fetchReadings();
  }, []);

  return (
    <IoTReadingsContainer>
      <h2>IoT Device Readings</h2>
      {readings.map((reading, index) => (
        <ReadingItem key={index}>
          <p>{reading.timestamp}: {reading.value}</p>
        </ReadingItem>
      ))}
    </IoTReadingsContainer>
  );
}

export default IoTReadingsScreen;