import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const WeatherScreenContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 20px;
`;

const Label = styled.label`
  margin: 10px 0;
  font-weight: bold;
`;

const WeatherData = styled.div`
  margin: 10px 0;
  font-size: 1.2em;
`;

function WeatherScreen() {
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.open-meteo.com/v1/forecast?latitude=23.2599&longitude=77.4126&current_weather=true');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        setWeatherData(data.current_weather);
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <WeatherScreenContainer>Loading...</WeatherScreenContainer>;
  }

  if (error) {
    return <WeatherScreenContainer>Error: {error}</WeatherScreenContainer>;
  }

  return (
    <WeatherScreenContainer>
      <Container>
        <h3>Weather in Bhopal</h3>
        <WeatherData>
          <Label>Temperature:</Label> {Math.round(weatherData.temperature)}C
        </WeatherData>
        <WeatherData>
          <Label>Wind Speed:</Label> {weatherData.windspeed} km/h
        </WeatherData>
        <WeatherData>
          <Label>Conditions:</Label> {weatherData.weathercode}
        </WeatherData>
      </Container>
    </WeatherScreenContainer>
  );
}

export default WeatherScreen;