import React, { useEffect, useRef, useState } from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import './GraphAndAnalyticsScreen.css'; // Import the CSS file

// Register necessary components
Chart.register(...registerables);

const GraphAndAnalyticsScreen = ({ pieChartData, irrigationTips }) => {
  const chartRef = useRef(null);
  const [chartData, setChartData] = useState({
    labels: ['No Data'],
    datasets: [{
      data: [1],
      backgroundColor: ['#d3d3d3'],
    }],
  });

  useEffect(() => {
    // Destroy existing chart instance if it exists
    if (chartRef.current) {
      chartRef.current.destroy();
    }

    // Create new chart instance
    chartRef.current = new Chart(document.getElementById('myPieChart'), {
      type: 'pie',
      data: chartData,
    });

    return () => {
      // Cleanup chart instance on component unmount
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [chartData]);

  useEffect(() => {
    if (pieChartData && pieChartData.datasets && pieChartData.datasets.length > 0) {
      setChartData(pieChartData);
    }
  }, [pieChartData]);

  return (
    <div className="container">
      <h2>Moisture Distribution</h2>
      <div className="chart-container">
        <canvas id="myPieChart"></canvas>
      </div>
      <div className="tips-container">
        <h2>Irrigation Tips</h2>
        <p>{irrigationTips || 'No tips available'}</p>
      </div>
    </div>
  );
};

export default GraphAndAnalyticsScreen;