import React, { useState, useEffect } from 'react';

function SettingScreen() {
  const [settings, setSettings] = useState({});
  const [connectivity, setConnectivity] = useState('wifi');

  useEffect(() => {
    // Fetch settings from API
    const fetchData = async () => {
      try {
        const response = await fetch('/api/settings');
        const data = await response.json();
        setSettings(data);
      } catch (error) {
        console.error('Error fetching settings:', error);
      }
    };

    fetchData();
  }, []);

  const handleUpdateSetting = (key, value) => {
    // Send a request to update a setting
    fetch('/api/settings', {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ key, value })
    })
    .then(response => response.json())
    .then(data => {
      setSettings(prevSettings => ({
        ...prevSettings,
        [key]: value
      }));
    })
    .catch(error => {
      console.error('Error updating setting:', error);
    });
  };

  const handleConnectivityChange = (event) => {
    const newConnectivity = event.target.value;
    setConnectivity(newConnectivity);
    handleUpdateSetting('connectivity', newConnectivity);
  };

  return (
    <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif' }}>
      <h1>Settings</h1>
      <div>
        <h2>Connectivity</h2>
        <label>
          <input
            type="radio"
            value="wifi"
            checked={connectivity === 'wifi'}
            onChange={handleConnectivityChange}
          />
          Wi-Fi
        </label>
        <label style={{ marginLeft: '20px' }}>
          <input
            type="radio"
            value="local"
            checked={connectivity === 'local'}
            onChange={handleConnectivityChange}
          />
          Local Network
        </label>
      </div>
      {/* Add more settings here */}
    </div>
  );
}

export default SettingScreen;