import React, { useState } from 'react';
import styled from 'styled-components';

const IoTControlContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
`;

const Button = styled.button`
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  font-size: 1em;
  width: 80%;
  margin-bottom: 10px;

  &:hover {
    background-color: #0056b3;
  }
`;

function IoTControlScreen() {
  const [status, setStatus] = useState('');

  const sendCommand = async (command) => {
    try {
      const response = await fetch('/api/iot-device', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ command }),
      });

      if (response.ok) {
        const result = await response.json();
        setStatus(result.status);
      } else {
        setStatus('Error sending command');
      }
    } catch (error) {
      setStatus('Error sending command');
    }
  };

  return (
    <IoTControlContainer>
      <h2>IoT Device Control</h2>
      <Button onClick={() => sendCommand('start')}>Start Device</Button>
      <Button onClick={() => sendCommand('stop')}>Stop Device</Button>
      <p>Status: {status}</p>
    </IoTControlContainer>
  );
}

export default IoTControlScreen;